.sidenav {
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color:blue;
  padding-top: 20px;
  display:flex;
  flex-direction: column;
  
  /* position:relative; */
}
.sidenav div{
  flex:1;
}
.sidenav ul i{
  margin:10px;
}
.sidenav h2 {
  color: white;
  text-align: center;
}

.sidenav ul {
  list-style-type: none;
  padding: 0;
  font-size: xx-large;
  margin-top:85px;
}

.sidenav ul li {
  padding: 10px ;
  text-align: center;
  justify-content: center;
}

.sidenav ul li a {
  color: white;
  text-decoration: none;
  display: block;
  border-radius: 10px;
  transition: 0.2s;
  padding:45px 22px;
}

.sidenav ul li a:hover {
  background-color: rgb(159, 255, 255);
  color:black;
}
.logo{
  position:absolute;
}

.logo img{
  width:100%;
}

.menu{
  display: flex;
  justify-content: center;
}
/* ================================ */
#head1{
  margin-left: 96px;
  margin-top: 0px;
}
#heading{
  background-color: rgb(27, 27, 117);
  padding: 20px;
  margin: 0px;
  color: white;
  font-family: 'Times New Roman', Times, serif;
}
#overall{
  margin-left: 190px;
  display: flex;
  /* margin: 200px; */
  margin-top: 30px;
  margin-right: 150px;
}
#overall div{
  flex:1;
  margin:20px;
  padding: 30px;
  font-size:70px;
  text-align: left;
}
#card1{
  background-color: rgb(255, 0, 0);
  border-radius:39px;
}
#card1:hover{
  box-shadow: 20px 40px 60px;
}
#card2{
  background-color: green;
  border-radius:39px;
}
#card2:hover{
  box-shadow: 20px 40px 60px;
}
#card3{
  background-color: blue;
  border-radius:39px;
}
#card3:hover{
  box-shadow: 20px 40px 60px;
}
#count{
  float: right;

}
.menu-options{
  display: grid;
  margin:10px 290px;
  grid-template-columns: repeat(3,1fr);
  font-family: 'Times New Roman', Times, serif;
  
}
.opts{
  color:navy;
  text-align: center;
  margin:30px;
  padding:30px;
  font-size: medium;
  padding-top: 5px;
}
.opts:hover{
  box-shadow: 20px 20px 80px;
}
.opts i, #icons{
  font-size:50px;
}
#para{
  padding-top: 15px;

}
h3{
  padding-top: 15px;
  font-family: 'Times New Roman', Times, serif;
}